body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  display: block;
  width:90vw;
  padding: 5vw;
}
h2{
  color:#999;
  width: 90vw;
  padding: 5vw;
}

#root div.quoteContainer {
  height:100vh;
  width: 100vw;
}

#root div.cardContainer {
  display:inline-block;
  padding: 30px 10px;
  transition:.5s ease-in-out;
}

#root div.cardContainer:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

#root div.card {
  height: 300px;
  width: 180px;
  background:#000;
}

.cardContainer h3 {
  margin:0;
  padding:10px 0;
}

.cardContainer p {
  margin:0;
  padding:0;
}

